.page-footer {
  padding-top: 249px;

  color: $font-color-dark;
  text-align: center;
}

.page-footer h2 {
  margin: 0;
  padding: 0;
}

.page-footer p {
  margin-top: 30px;
  margin-bottom: 41px;
  margin-right: 31.25%;
  margin-left: 30.21%;
  padding: 0;

  @include text (17px, 31px, normal);

  color: rgba(#000000, 0.7%);
}

.page-footer__map {
  margin: 0 auto;
  padding: 0;
  width: 957px;
  height: 530px;

  background-image: url("../../img/map-desktop@1x.jpg");
}

@media (max-width: $tablet-width) {
  .page-footer {
    padding-top: 155px;
  }

  .page-footer h2 {
    @include text (24px, 30px, 600);
  }

  .page-footer p {
    margin: 0;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .page-footer__map {
    width: 698px;
    height: 545px;

    background-image: url("../../img/map-tablet@1x.jpg");
  }
}

@media (max-width: $mobile-width) {
  .page-footer {
    padding-top: 119px;
  }

  .page-footer h2 {
    @include text (18px, 30px, 600);
  }

  .page-footer p {
    width: 81.56%;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 25px;

    @include text (17px, 31px, normal);
  }

  .page-footer__map {
    width: 100%;
    height: 250px;

    background-image: url("../../img/map-tablet@1x.jpg");
  }
}
